import { Form, Icon, Input,Row,Col, Button, Select } from 'antd';
import React,{Component} from 'react';
import {Link} from 'react-router-dom';
import '../css/form.css';
const FormItem = Form.Item;
const { TextArea } = Input;

const Option = Select.Option;
class NormalLoginForm extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Row style={{display:'flex',justifyContent:'center'}}>
          <Col md={12}>
            <p className="title-header">Nhp thng tin ca bn</p>
            <Form onSubmit={this.handleSubmit} className="login-form" layout="vertical">
                <FormItem label="H v tn:" >
                    {getFieldDecorator('fullname', {
                        rules: [{ required: true, message: 'Vui lng nhp h v tn!' }],
                    })(
                        <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="H v tn" />
                    )}
                </FormItem>
                <FormItem label="S in thoi:" >
                    {getFieldDecorator('phone', {
                        rules: [{ required: true, message: 'Vui lng nhp s in thoi' }],
                    })(
                        <Input prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />} type="number" placeholder="S in thoi" />
                    )}
                </FormItem>
                <FormItem label="Email:" >
                    {getFieldDecorator('email', {
                        rules: [{ required: true, message: 'Vui lng nhp email!' }],
                    })(
                        <Input prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />} placeholder="Email" />
                    )}
                </FormItem>
                <FormItem label="Hnh thc thanh ton:" >
                    {getFieldDecorator('paymentMethod', {
                        rules: [{ required: true, message: 'Vui lng chn hnh thc thanh ton!' }],
                    })(
                        <Select
                            // value={state.currency}
                            // size={size}
                            defaultValue="AT_PRODUCER"
                            style={{ width: '100%' }}
                            // onChange={this.handleCurrencyChange}
                            >
                                <Option value="AT_HOME">Nhn xe ti nh</Option>
                                <Option value="AT_PRODUCER">Nhn xe ti i l</Option>
                        </Select>    
                    )}
                </FormItem>
                <FormItem label="Lu :" >
                    <TextArea rows={4} />
                </FormItem>
                <FormItem>
                    <Link to="/final">
                        <Button style={{float:'right'}} type="primary" htmlType="submit" className="login-form-button">
                            Hon tt
                        </Button>
                    </Link>
                </FormItem>
            </Form>
          </Col>
      </Row>
    );
  }
}

const WrappedNormalLoginForm = Form.create()(NormalLoginForm);

export default WrappedNormalLoginForm ;