import { Form,Row,Col, } from 'antd';
import React,{Component} from 'react';
import '../css/form.css';
import iconSuccess from 'images/icon/step3.png';
class NormalLoginForm extends Component {
  handleSubmit = (e) => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  }

  render() {
    //   0
    return (
      <Row style={{display:'flex',justifyContent:'center',flexDirection:'column'}}>
          <Col md={24}>
            <Row className="content_order_wrapper">
                <Col md={16}>
                    <img src={iconSuccess} width="180" />
                    <p className="title-header-final">t xe thnh cng</p>
                </Col>
                <Col md={16}>
                    <p >M t xe ca bn:</p>
                    <p className="code-order">HNIOT181110166</p>
                </Col>
                <Col md={16}>
                    <p >
                        Yu cu t xe ca bn  c h thng ghi nhn.
                        Chng ti s lin lc li vi bn  thng bo tnh trng 
                        ca xe t nht 6h trc thi im thu xe.
                    </p>
                </Col>
            </Row>
          </Col>
      </Row>
    );
  }
}

const WrappedNormalLoginForm = Form.create()(NormalLoginForm);

export default WrappedNormalLoginForm ;