import { Menu, Icon } from 'antd';
import React , {Component} from 'react';
import {Link} from 'react-router-dom';
import history from '../../../../history';
import './index.css';
import * as CONST_VARIABLE from 'utils/const/index';
const SubMenu = Menu.SubMenu;
const MenuItemGroup = Menu.ItemGroup;

class Sider extends Component {

  handleClick = (e) => {
    history.push( this.props.url+'/'+ e.key);
    window.location.reload();
  }


  render() {
    const role = sessionStorage.getItem(CONST_VARIABLE.ROLE_ACCOUNT);
    return (
      <Menu
        onClick={this.handleClick}
        style={{ width: 256 }}
        defaultSelectedKeys={['1']}
        defaultOpenKeys={(role==='PARTNER')?['sub1', 'sub4']:['sub1','sub2', 'sub4']}
        mode="inline"
      >
        <SubMenu key="sub1" title={<span><Icon type="mail" /><span>Thng tin</span></span>}>
            <Menu.Item key="5">Cp nht mt khu</Menu.Item>
            <Menu.Item key="6">Cp nht thng tin</Menu.Item>
            <SubMenu key="sub3" title="Gii thiu">
                <Menu.Item key="7">H thng</Menu.Item>
                <Menu.Item key="8">C nhn</Menu.Item>
            </SubMenu>
        </SubMenu>

        <SubMenu key="sub2" 
            // disabled={(role==='PARTNER')?true:false}
            title={<span><Icon type="appstore" /><span>Qun l ca admin</span></span>}>
          {/* <MenuItemGroup key="g1" title="Module"> */}
            <Menu.Item disabled={(role==='PARTNER')?true:false} key="car">
                Tt c xe ca h thng
            </Menu.Item>

            <Menu.Item disabled={(role==='PARTNER')?true:false} key="partner">
                  Qun l i tc
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="oders">
                  Qun l n hng
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="customer">
                  Qun l khch hng
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="customercar">
                  Khch hng cho th xe
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="feature">
                  Tnh nng xe
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="packages">
                  Gi dch v
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="payment">
                  Phng thc thanh ton
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="procedure">
                  Th tc t xe
              </Menu.Item>

              <Menu.Item disabled={(role==='PARTNER')?true:false} key="partnerpackage">
                  Gi partner
              </Menu.Item>
          {/* </MenuItemGroup> */}
        </SubMenu>

        <SubMenu key="sub4" title={<span><Icon type="mail" /><span>Qun l ca partner</span></span>}>
            <Menu.Item key="CarOfPartner">Qun l xe</Menu.Item>
            <Menu.Item key="OrdersOfPartner">n hng</Menu.Item>
            <Menu.Item key="InfoService">Thng tin dch v</Menu.Item>
        </SubMenu>
      </Menu>
    );
  }
}
export default Sider;