import React, {Component} from 'react';
import FormContent from 'components/ptp__form-layout';
import {layouts} from '../layout';
import {Row, Col, Button, Icon} from 'antd';
import { withRouter} from 'react-router-dom';
import { connect } from 'react-redux';


const edit_view = 0, edit_submit= 1;
class FormEditContent extends Component{
    constructor(props){
        super(props);

        this.state={
            edit:edit_view, 
            dataSubmit:{
            },
            car:null,
            id:'',

        }
    }

    changeView = () => {
        this.setState({edit: edit_submit});
    }

    handleSubmit = (values) => {
        this.setState({edit: edit_view});
        
        if(values!=='error'){
            const id= this.props.id;
            const obj= {
                id: id,
            }
            this.props.onSubmitEdit( id, {...values, ...obj});
        }
        // this.setState({edit: edit_view});
    }

    render(){
        const { edit,  } = this.state;
        const { partner } = this.props;
        const classNames="field-no-radius form-light";
        const type="vertical";
        const trigger =[
            {
                attrbTrigger:'save',
                render:{
                    type:'BUTTON',
                    size:'default',
                    label:'Lu li',
                    icon:'download',
                }
            },  
        ]
        const actions=[
            {
                attrbtn: 'save',
                content: (val) => this.handleSubmit(val)
            },
        ]
        const listFields=[
            {
                attrbField:'nameCompany',
                data:{
                    dataType:'TEXT',
                    defaultValue:partner.nameCompany
                },
                render:{
                    placeholder:'Vui lng nhp cng ty',
                    label:'Tn cng ty: ',
                },
                validation:{
                    rules: [
                        {
                          required: true,
                          message: 'Trng d liu ny l bt buc!!!'
                        }
                    ],
                },
                action:{
                }
            },
            {
                attrbField:'email',
                data:{
                    dataType:'EMAIL',
                   
                    defaultValue:partner.email,
                    validation:{
                        rules: [
                            {
                              required: true,
                              message: 'Trng d liu ny l bt buc!!!'
                            },
                            {
                                type: 'email',
                                message: 'Bn phi nhp ng nh dng email!!!'
                              },
                        ],
                    },
                },
                render:{
                    placeholder:'Vui lng nhp email ca bn',
                    label:'Email: ',
                },
               
                action:{
                }
            },
            {
                attrbField:'phoneNumber',
                data:{
                    dataType:'PHONE',
                    defaultValue:partner.phoneNumber
                },
                render:{
                    placeholder:'Vui lng nhp s in thoi ',
                    label:'S in thoi: ',

                },
                action:{
                }
            },
            
            {
                attrbField:'partnerPayment',
                data:{
                    dataType:'SELECT',
                    config:{
                        options:[
                            {
                                text:'Thanh ton online',
                                value:'online',
                            },
                            {
                                text:'Tin mt',
                                value:'tienmat',
                            },
                        ]
                    },
                    validation:{
                        rules: [
                            {
                              required: true,
                              message: 'Trng d liu ny l bt buc!!!'
                            }
                        ],
                    },
                    defaultValue:'online',
                },
                render:{
                    placeholder:'Vui lng chn phng thc thanh ton',
                    label:'Phng thc thanh ton: ',
                },
                action:{
                }
            },
            
        ] 
       
        return(
            <Row >
                <Col md={24}>
                    <FormContent
                        layouts={layouts}
                        actions={actions} 
                        trigger={trigger} 
                        classNames={classNames} 
                        type={type} 
                        listFields={listFields}
                        submit= {edit}
                        formID= "editCar"
                        mode= "edit"
                        handleSubmit= {this.handleSubmit}
                    />
                </Col>
                <Col md={24}>
                    <Button onClick={()=>this.changeView()}>
                        <Icon type="save"></Icon> Save
                    </Button>
                </Col>
            </Row>
            
            
        )
    }
}


const mapStateToProps = state => {
    return {
    }
}

const mapDispatchToProps = (dispatch, props) => {
    return {

    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(FormEditContent));